*{margin: 0; padding: 0;}
li{list-style: none;}

.header{position: relative; height: 60px;}
.header h1{position: absolute; left: 50%; top: 10px ; transform: translateX(-50%);}
.header img{height: 40px;}

.topBanner{position: relative;}
.topBanner img{width: 100%;}

.product{position: relative;}
.product h2{text-align: center; margin: 50px auto;}
.product .proWrap{position: relative; width: 90%; margin: 0 auto; display: flex; justify-content:space-between; }
.product .proWrap .porList{width: 23%;}
.product .proImg img{width: 100%;}
.product .proText{padding: 10px 0;}
.product .proText h3{font-size: 14px; margin-bottom: 20px; }
.product .proText .price{font-size: 14px; color: #999; display: flex; justify-content: space-between;}

.about{position: relative; width: 90%; margin: 100px auto; display: flex; justify-content: space-between;}
.aboutImg{width: 60%;}
.aboutImg img{width: 100%;}
.aboutText{width: 40%; text-align: center; box-sizing: border-box; padding: 0 50px; display: flex; align-items: center;}
.aboutText h2{font-size: 50px; font-weight: normal;}



.footer{position: relative; height: 80px; background-color: #222; text-align: center; line-height: 80px; color: #FFF; font-size: 12px;}

.lightBox{position: fixed; width: 100%; height: 100%; background-color: rgba(0,0,0,0.8); display: flex; justify-content: center; align-items: center;left: 0; top: 0;}
.lightBox .lightInner{position: relative; width: 50%; background-color: #FFF; border-radius: 20px; padding: 20px; display: flex; justify-content: space-between;}
.lightBox .lightInner div{width: 48%; display: flex; flex-direction: column; justify-content: center;}
.lightBox .lightInner div h2{font-size: 20px;}
.lightBox .closeBtn{position: absolute; right: 20px; bottom: 20px; border: none; background-color: #000; color: #FFF; font-size: 12px; padding: 2px 10px;}

.visited{position: relative; width: 90%; margin: 100px auto;}
.visited h2{text-align: center;}
.visited .inputBox{position: relative; margin: 30px 0; display: flex; align-items: center;}
.visited .inputBox input{width:calc(100% - 80px);border: 1px solid #CCC; height: 25px; position: relative; }
.visited .inputBox button{width: 80px; height: 27px;  background-color: #000; color: #FFF; border: none;}
.review{position: relative;}

.review .reviewList{ padding: 10px 20px 10px 10px; box-sizing: border-box; border: 1px solid #EEE; border-radius: 5px;display: flex; font-size: 14px; position: relative;}
.review .reviewList .del{position: absolute; right: 10px; bottom: 15px; color: #EEE; background-color: #000; color: #FFF;line-height: 1em; text-align: center;line-height: 15px; font-size: 12px; width: 15px; height: 15px; border-radius: 50%;}

@media screen and (max-width: 600px){
    .product .proWrap{flex-wrap: wrap;}
    .product .proWrap .porList{width: 49%;}

    .about{position: relative; width: 90%; margin: 100px auto; display: flex; flex-direction: column;}
    .aboutImg{width: 60%; }
    .aboutImg img{width: 100%;}
    .aboutText{width: 70%; text-align: center; box-sizing: border-box; padding: 0 20px; display: flex; align-items: center; align-self: flex-end;}
    .aboutText h2{font-size: 8vw; font-weight: normal;}

    .lightBox .lightInner{position: relative; width: 80%; background-color: #FFF; border-radius: 20px; padding: 20px; display: flex; justify-content: space-between;}
    .lightBox .lightInner div h2{font-size: 18px;}
}
    